const traverse = require('./traverse');
const objectPath = require('object-path');

function makeSameBaseCollection(obj) {
  if (obj && typeof obj === 'object') {
    if (obj instanceof Array) {
      return [];
    } else {
      return {};
    }
  }

  return undefined;
}

function isEmptyCollection(obj) {
  return !obj || obj.length === 0 || Object.keys(obj).length === 0;
}

const targetProperties = ['Description', 'Teaser', 'Name', 'Id', 'ButtonText'];

exports.extractText = function extractText(obj) {
  if (obj && typeof obj === 'object') {
    let newCurrent = makeSameBaseCollection(obj);
    for (let key in obj) {
      let child = extractText(obj[key]);
      if (child) {
        newCurrent[key] = child;
      }
    }

    if (obj.Id && obj.Description) {
      targetProperties.forEach((property) => {
        newCurrent[property] = obj[property];
      });
    }

    if (isEmptyCollection(newCurrent)) {
      return undefined;
    }

    return newCurrent;
  }
}

exports.mergeText = function(original, toMerge) {
  let result = JSON.parse(JSON.stringify(original));

  traverse.widthObjectPath(toMerge, (obj, path) => {
    if (typeof obj != 'object') {
      objectPath.set(result, path, obj);
    }
  })

  return result;
}