const traverse = require('./traverse');

module.exports = function extractTextForTranslate(events) {
  const targetProperties = ['Description', 'Teaser', 'Name'];
  const result = {};

  const callback = (obj, objectPath) => {
    if (obj.Id) {
      targetProperties.forEach(property => {
        if (obj[property]) {
          const address = [obj.Id, objectPath, property].join('-');
          if (result[address]) {
            throw new Error('Same Address, must be invalid events.json');
          }
          result[address] = obj[property];
        }
      });
    }
  };
  traverse.widthObjectPath(events, callback);

  return result;
}
